.SignUp-outer {
  margin-top: 5%;
  display: flex;
  flex-direction: column;
}
.SignUp-main {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.SignUP {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.tagLine-align {
  display: flex;
  flex-direction: row;
  justify-content: baseline;
  align-items: center;
}
.tagLine-align h5 {
  padding-top: 1rem;
  color: #6441a5;
  font-weight: bold;
  font-style: italic;
}
.tagLine-align h5:hover {
  cursor: pointer;
}
.tagLine-align h1 {
  padding-left: 2rem;
}
#bg-margin {
  margin-left: 1rem;
}
.SignUpForm {
  margin-right: 2rem;
  margin-top: 8%;
  margin-bottom: 30%;
  padding-top: 15px;
  padding-left: 25px;
  padding-right: 25px;
  box-shadow: 10px 10px 50px black;
  background-color: #23272a;
  width: 26rem;
  height: 37rem;
  color: white;
  border: #6441a5 2px solid;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}

.SignUpBTN {
  background-color: #559a48;
  height: 2.5rem;
  width: 8rem;
  font-size: 1em;
}
.Align p {
  font-style: italic;
  font-size: 15px;
  text-align: center;
  color: #6441a5;
}
footer {
  background-color: #23272a;
  width: 100%;
  height: 8rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
footer h3 {
  color: whitesmoke;
  font-style: italic;
  font-size: 20px;
}
@media only screen and (max-width: 800px) {
  .SignUpForm {
    width: 24rem;
    height: 36rem;
    margin-right: 0px;
  }
  .SignUp-main {
    z-index: 5;
    padding-top: 0%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  .bg- {
    width: 400px;
    margin-top: 5%;
  }
  footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
