.login-outer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Login-main {
  margin-left: 0.5rem;
  z-index: 5;
  padding-top: 5.5%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.Login {
}
.bg- {
  width: 500px;
}

.loginForm {
  width: 24rem;
  box-shadow: 10px 10px 50px black;
  height: 30rem;
  background-color: #23272a;
  color: whitesmoke;
  border: #6441a5 2px solid;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  margin-top: 8%;
  padding-top: 1.5rem;
  padding-left: 2.5rem;
  padding-right: 1.5rem;
  margin-bottom: 20%;
}
.inputFields {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
form h1 {
  font-size: 3rem;
  text-shadow: 1.5px 1.5px 25px #6441a5;
  color: #6441a5;
  text-align: center;
  font-weight: bold;
  margin: 1.5rem;
  margin-bottom: 3rem;
}
form div input {
  margin-bottom: 15px;
  height: 35px;
  border-radius: 5px;
}
.btnAlign {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 1rem;
  margin-left: 3.5rem;
}
.errorAtLogin {
  margin-top: ;
  color: red;
  font-size: 15px;
  font-weight: bold;
  font-style: italic;
}
.SignInBTN {
  color: white;
  width: 100px;
  height: 2.5rem;
  width: 8rem;
  background-color: #6441a5;
}
.Align {
  font-style: italic;
  font-size: 15px;
  text-align: center;
  color: #6441a5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#SignUp {
  color: #559a48;
}
#SignUp:hover {
  cursor: pointer;
}
footer {
  background-color: #23272a;
  width: 100%;
  height: 8rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
footer h3 {
  color: whitesmoke;
  font-style: italic;
  font-size: 20px;
}
.social-links {
  display: flex;
  flex-direction: row;
}
.icon-wrapper {
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 3rem;
}
.icon-wrapper:hover {
  cursor: pointer;
}
.bg-wrapper {
  background-color: rgba(255, 255, 255, 0.9);
}
.bg-wrapper:hover {
  background-color: rgba(255, 255, 255);
}
.icon {
  width: 1.5rem;
}
.git-icon {
  width: 2.3rem;
}
.footer-site-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer-site-info p {
  color: whitesmoke;
  font-size: 0.8rem;
  font-style: italic;
}

@media only screen and (max-width: 800px) {
  .Login-main {
    z-index: 5;
    width: 23rem;
    padding-right: 0px;
    padding-top: 0%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  .bg- {
    width: 400px;
  }
  footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
