@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

body {
  font-family: "Montserrat", sans-serif;
  margin: 0px;
  padding: 0px;
  z-index: 0;
  background-color: #393c43;
}
.header {
  z-index: 1;
  width: 100%;
  height: 70px;
  z-index: 10;
  overflow: hidden;
  padding: 0px;
  margin: 0px;
  font-size: 20px;
  font-weight: bold;
  font-style: italic;
  text-align: center;
  background-color: #23272a;
  color: #6441a5;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.3);
}
.tagline-enclosure h3 {
  margin: 0px;
}
.tagline-enclosure p {
  color: whitesmoke;
  margin: 0px;
  font-size: 15px;
}

.navbar {
  z-index: 2;
  margin-top: 4.2rem;
  height: calc(100% - 4.2rem);
  width: 5.5rem;
  overflow: hidden;
  position: fixed;
  background-color: #23272a;
  color: white;
}
.navbar-nav {
  padding: 0px;
  margin: 0px;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.navitem {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.spaceSeperator {
  border-radius: 6px;
  height: 3px;
  background-color: white;
  opacity: 10%;
}

.postSection {
  margin-top: 4.2rem;
}
.AllPosts {
}
.Logo {
  height: 50px;
  width: 50px;
  background-image: url("DEAD-MUNSHI.png");
  background-size: 50px;
  background-repeat: no-repeat;
  margin-left: 1rem;
}
.UserPosts {
  background-color: rgba(255, 255, 255, 0.1);
  width: 55px;
  height: 55px;
  border-radius: 70px;
  padding-right: 5px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: border-radius 300ms ease-in-out;
  transition-delay: 0ms;
}
.UserPosts:hover {
  border-radius: 18px;
  cursor: pointer;
  background-color: #37a55a;
}
.newPostSvg {
  filter: invert(37%) sepia(100%) saturate(200%) hue-rotate(71deg)
    brightness(120%) contrast(96%);
  width: 2rem;
  height: 2rem;
  padding: 0px;
  transition: filter 300ms ease-in-out;
  transition-delay: 0ms;
}
.newPostSvg:hover {
  filter: invert(69%) sepia(25%) saturate(0%) hue-rotate(313deg)
    brightness(500%) contrast(99%);
}

.logout {
  background-color: rgba(255, 255, 255, 0.1);
  width: 55px;
  height: 55px;
  border-radius: 70px;
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: border-radius 300ms ease-in-out;
  transition-delay: 0ms;
}
.logout:hover {
  border-radius: 18px;
  cursor: pointer;
  background-color: #37a55a;
}
.logoutSvg {
  filter: invert(37%) sepia(100%) saturate(200%) hue-rotate(71deg)
    brightness(120%) contrast(96%);
  width: 25px;
  height: 30px;
  transition: filter 300ms ease-in-out;
  transition-delay: 0ms;
}
.logoutSvg:hover {
  filter: invert(69%) sepia(25%) saturate(0%) hue-rotate(313deg)
    brightness(500%) contrast(99%);
}

.Loader {
  margin-left: 15%;
}
.User {
  padding-top: 7px;
  opacity: 80%;
  border-radius: 20px;
  background-color: white;
  color: black;
  height: 40px;
  width: 160px;
}
.right-side-contents {
  margin-right: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: baseline;
  align-items: center;
}
.userName {
  color: whitesmoke;
  margin-right: 15px;
  display: flex;
  flex-direction: row;
  justify-content: baseline;
  align-items: center;
}
.userName h3 {
  padding-top: 5px;
  font-size: 15px;
}
.userSvg {
  width: 20px;
  margin-right: 12px;
}
.reloadSvg {
  padding-bottom: 5px;
  height: 1.5rem;
  filter: invert(25%) sepia(58%) saturate(1651%) hue-rotate(237deg)
    brightness(92%) contrast(87%);
  cursor: pointer;
}
.reloadSvg:hover {
  filter: invert(33%) sepia(94%) saturate(757%) hue-rotate(229deg)
    brightness(89%) contrast(89%);
}
.posts_align {
  padding-top: 6%;
}

@media only screen and (max-width: 1200px) {
  .navbar {
    padding: 0px;
    padding-left: 1rem;
    bottom: 0;
    width: 100vw;
    height: 5.5rem;
    position: fixed;
  }
  .navbar-nav {
    flex-direction: row;
  }
  .navitem {
    flex-direction: row;
    justify-content: baseline;
  }
  .spaceSeperator {
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 7px;
    width: 4px;
    height: 4rem;
  }
  .navitem:last-child {
    margin-left: auto;
  }
}
@media only screen and (max-width: 600px) {
  .tagline-enclosure {
    display: none;
  }
  .header {
    height: 70px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (min-width: 1200px) {
  .spaceSeperator {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .postSection {
  }
  .navbar {
    padding-top: 1rem;
  }
  .navitem:last-child {
    margin-top: auto;
  }
}
