.GLaoding {
  z-index: 1000;
  position: absolute;
  background-color: #23272a;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  opacity: 1;
}
.spin-logo {
  -webkit-animation: spin 0.5s linear infinite;
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.GLaoding img {
  width: 100px;
}
