::-webkit-scrollbar {
  width: 0.35rem;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #6441a5;
}
:-webkit-scrollbar-thumb {
  background-color: #6441a5;
  height: 20px;
}
::-webkit-scrollbar-thumb:hover {
  background: #6441a5;
}
.postSection {
  padding: 5px;
  padding-top: 20px;
  position: fixed;
  z-index: 1;
  margin-left: 5.5rem;
  width: 22%;
  height: 100%;
  background-color: #2c2f33;
  overflow: auto;
}
.HeaderPostsCategories {
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.postsCategories {
  border-radius: 10px;
  padding-left: 1rem;
  padding-right: 1rem;
  color: #559a48;
  background-color: #393c43;
  width: fit-content;
  margin: 5px;
}
.cancel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  border-radius: 30px;
  background-color: white;
  opacity: 60%;
}
.cancel:hover {
  opacity: 50%;
  cursor: pointer;
}
.cancelIcon {
  padding: 2px;
  height: 1rem;
  width: 1rem;
}
.post-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  outline: none;
  background-color: #b9a3e3;
  width: 60px;
  color: white;
}
.createPost {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.post-text-area {
  min-height: 130px;
  max-height: 400px;
  margin-bottom: 5px;
}
@media only screen and (max-width: 1200px) {
  .postSection {
    margin-top: 2%;
    z-index: 10;
    box-shadow: 5px 10px 100px black;
    margin-left: 10%;
    width: 80%;
    height: 100%;
    padding-left: 50px;
    padding-right: 50px;
    border-radius: 20px;
  }
}
@media only screen and (max-width: 420px) {
  .postSection {
    margin-top: 0%;
    z-index: 10;
    box-shadow: 5px 10px 100px black;
    margin-left: 0%;
    width: 100%;
    height: 100%;
    padding-left: 50px;
    padding-right: 50px;
    border-radius: 20px;
  }
} ;
