.PostsContainer {
  z-index: 0;
  margin-top: 2rem;
}

.GPOST {
  padding: 1rem;
  border-radius: 15px;
  margin-bottom: 15px;
  background-color: white;
  width: 18rem;
  min-height: 15rem;
  max-height: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  transition: box-shadow 200ms ease-in-out;
  transition-delay: 0ms;
}
.GPOST:hover {
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.3);
}
.post_header {
  display: flex;
  flex-direction: row;
  justify-content: baseline;
  justify-content: space-between;
  align-items: center;
}
.post_user_info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.post_user_info h3 {
  padding-top: 0.8rem;
  font-size: 16px;
  font-style: italic;
  font-weight: bold;
  color: #febf10;
}
.global_user_svg {
  margin-right: 0.3rem;
  width: 20px;
}
.check_mark_svg {
  width: 15px;
  filter: invert(37%) sepia(100%) saturate(200%) hue-rotate(71deg)
    brightness(120%) contrast(96%);
}
.post_content h4 {
  font-size: 20px;
  font-weight: bold;
  color: #37a55a;
}
.post_date {
  font-size: 0.7rem;
  font-style: italic;
  font-weight: bold;
}
.seeMore {
  color: blue;
}
.PostImage {
  width: 200px;
}
